:global html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

:global body {
  margin: 0;
}

:global h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

:global hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

:global pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

:global a {
  background-color: transparent;
}

:global abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

:global b,
:global strong {
  font-weight: bolder;
}

:global code,
:global kbd,
:global samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

:global small {
  font-size: 80%;
}

:global sub,
:global sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

:global sub {
  bottom: -0.25em;
}

:global sup {
  top: -0.5em;
}

:global img {
  border-style: none;
}

:global button,
:global input,
:global optgroup,
:global select,
:global textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

:global button,
:global input {
  /* 1 */
  overflow: visible;
}

:global button,
:global select {
  /* 1 */
  text-transform: none;
}

:global button,
:global [type="button"],
:global [type="reset"],
:global [type="submit"] {
  -webkit-appearance: button;
}

:global button::-moz-focus-inner,
:global [type="button"]::-moz-focus-inner,
:global [type="reset"]::-moz-focus-inner,
:global [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:global button:-moz-focusring,
:global [type="button"]:-moz-focusring,
:global [type="reset"]:-moz-focusring,
:global [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:global fieldset {
  padding: 0.35em 0.75em 0.625em;
}

:global legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

:global progress {
  vertical-align: baseline;
}

:global textarea {
  overflow: auto;
}

:global [type="checkbox"],
:global [type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

:global [type="number"]::-webkit-inner-spin-button,
:global [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

:global [type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

:global [type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

:global ::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

:global details {
  display: block;
}

:global summary {
  display: list-item;
}

:global template {
  display: none;
}

:global [hidden] {
  display: none;
}

:global html {
  font-size: 62.5%;
}

:global html,
:global body,
:global #root {
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100vh;
}

:global body {
  background-color: #393D43;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 1.6rem;
  -webkit-touch-callout: none;
  -webkit-font-smoothing: antialiased;
}

:global body * {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

:global input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  background: transparent;
}

:global input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

:global input[type=range]:focus {
  outline: none;
}

:global input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
